.header-wrapper {
  display: flex;
  justify-content: center; }

.header {
  position: relative;
  max-width: 1620px;
  width: 100%; }

.menu {
  display: flex;
  max-width: 1620px;
  width: 100%;
  color: #fff;
  border-top: 30px solid #edf0f4;
  height: 56px;
  position: fixed;
  z-index: 200; }

.menu_scroll {
  background-image: linear-gradient(-225deg, #3d3bff 0%, #3e3cec 48%, #8200ff 100%);
  height: 56px; }

.menu__button {
  flex: 1;
  padding: 12px 10px 12px 10px; }
  .menu__button img {
    cursor: pointer; }

.menu__logo {
  padding: 16px 0px;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 1px; }

.menu__home-link {
  color: #fff; }

.menu__beta-word {
  font-size: 10px;
  font-weight: 400; }

.menu__contacts {
  flex: 30;
  text-align: right;
  padding-right: 15px; }

.menu__phone {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin-top: 16px;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 1px;
  cursor: pointer; }

.menu__social {
  width: 25px;
  padding-top: 15px;
  padding-left: 10px;
  cursor: pointer; }



