@media (max-width: 1680px) {
  h1 {
    font-size: 50px; }
  .header, .menu, .preview, .bordered-visible-box {
    max-width: 1316px; }
  .content__works h3 {
    font-size: 42px; }
  .content__works .row__block > p {
    font-size: 25px; }
  .content {
    padding-top: 20px;
    padding-bottom: 20px; }
  .content__about {
    padding-top: 100px;
    padding-bottom: 5px; }
  .content__works {
    padding-top: 105px;
    padding-bottom: 15px; }
  .content__contacts {
    padding-top: 105px;
    padding-bottom: 40px; }
    .content__contacts p {
      font-size: 28px; }
  .content__page-not-found {
    padding-top: 105px;
    padding-bottom: 35px; }
  .row__text-block {
    font-size: 25px;
    padding-top: 0px; }
  .preview__text {
    font-size: 63px; } }

@media (max-width: 1375px) {
  .page {
    margin: 0px 30px; }
  .content__works h3 {
    font-size: 3vw; }
  .content__works .row__block > p {
    font-size: 1.8vw; }
  .row__text-block {
    font-size: 1.8vw; }
  .preview__text {
    font-size: 4.5vw; } }

@media (max-width: 1150px) {
  .content__works h3 {
    font-size: 2.7vw; }
  .content__works .row__block > p {
    font-size: 1.6vw; }
  .row__text-block {
    font-size: 1.7vw; } }

@media (max-width: 900px) {
  .page {
    margin: 0; }
  .menu {
    border-top: none; }
  .bordered-visible-box {
    margin-bottom: 0px; } }



