.content-box {
  width: 100%;
  position: relative; }

.bordered-visible-box {
  max-width: 1620px;
  width: 100%;
  position: relative;
  margin-bottom: 30px;
  background-color: #fff;
  overflow-x: hidden; }

.left-menu {
  width: 210px;
  height: 100%;
  position: absolute;
  left: -210px;
  z-index: 0;
  background-color: #fff;
  transition: left .5s ease-in-out; }
  .left-menu ul {
    padding-top: 140px; }
  .left-menu li {
    margin-bottom: 25px;
    font-size: 1.5em;
    font-weight: 200; }

.left-menu_padding-top ul {
  padding-top: 60px; }

.line {
  width: 100%;
  height: 1px;
  margin: 0;
  border: none;
  background: #edf0f4; }

.open {
  left: 0px; }



